/* eslint-disable no-underscore-dangle */
import { Corner, MDCMenu } from '@material/menu';
import { MDCMenuSurface, CornerBit } from '@material/menu-surface';
import clsx from 'clsx';
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
    forwardRef,
    useImperativeHandle
} from 'react';
import { edsRandomId, ConditionalWrapper } from '@element/react-common';
import { Portal } from '@element/react-portal';
import { MenuPropTypes, MenuDefaultProps } from './generated/propTypes';
import MenuAnchor from './MenuAnchor';
import MenuContext from './MenuContext';

const triggerWrapper = (anchorProps, trigger, anchorRef) => children =>
    (
        <MenuAnchor {...anchorProps} ref={anchorRef}>
            {trigger}
            {children}
        </MenuAnchor>
    );

const portalWrapper = hoistToBody => children =>
    <Portal disabled={!hoistToBody}>{children}</Portal>;

const Menu = forwardRef((props, externalRef) => {
    const {
        absolutePositionX,
        absolutePositionY,
        anchorCorner,
        anchorMargin,
        anchorProps,
        children,
        className,
        direction,
        fixed,
        fullScreen,
        fullWidth,
        handleBodyClick,
        hoistToBody,
        iconMenu,
        id,
        maxHeight,
        onClose,
        onOpen,
        onSelect,
        open,
        quickOpen,
        style,
        surfaceOnly,
        themeColor,
        trigger,
        unexposed,
        wrapFocus,
        ...customProps
    } = props;

    const [idActual] = useState(() => id || edsRandomId());
    const [openUp, setOpenUp] = useState();

    const ref = useRef();
    const mdcMenu = useRef();
    const anchorRef = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    const handleOnOpen = useCallback(
        e => {
            const corner = surfaceOnly
                ? mdcMenu.current.foundation.getoriginCorner()
                : mdcMenu.current.menuSurface_.foundation.getoriginCorner();
            const verticalAlignment =
                (surfaceOnly &&
                    mdcMenu.current.foundation.hasBit(
                        corner,
                        CornerBit.BOTTOM
                    )) ||
                (!surfaceOnly &&
                    mdcMenu.current.menuSurface_.foundation.hasBit(
                        corner,
                        CornerBit.BOTTOM
                    ))
                    ? 'bottom'
                    : 'top';
            setOpenUp(verticalAlignment === 'bottom');

            onOpen && onOpen(e);
        },
        [onOpen, surfaceOnly]
    );

    const handleSelect = useCallback(
        e => {
            onSelect && onSelect(e.detail);
        },
        [onSelect]
    );

    useEffect(() => {
        mdcMenu.current = surfaceOnly
            ? MDCMenuSurface.attachTo(ref.current)
            : MDCMenu.attachTo(ref.current);

        return () => {
            mdcMenu.current.destroy();
        };
    }, [surfaceOnly]);

    useEffect(() => {
        if (!surfaceOnly) {
            mdcMenu.current.listen('MDCMenu:selected', handleSelect);
        }
        return () => {
            if (!surfaceOnly) {
                mdcMenu.current.unlisten('MDCMenu:selected', handleSelect);
            }
        };
    }, [handleSelect, surfaceOnly]);

    useEffect(() => {
        mdcMenu.current.listen('MDCMenuSurface:closed', onClose);
        return () => {
            mdcMenu.current.unlisten('MDCMenuSurface:closed', onClose);
        };
    }, [onClose]);

    useEffect(() => {
        mdcMenu.current.listen('MDCMenuSurface:opened', handleOnOpen);
        return () => {
            mdcMenu.current.unlisten('MDCMenuSurface:opened', handleOnOpen);
        };
    }, [handleOnOpen]);

    useEffect(() => {
        if (!surfaceOnly) {
            mdcMenu.current.menuSurface_.foundation.setMaxHeight(maxHeight);
        } else {
            mdcMenu.current.foundation.setMaxHeight(maxHeight);
        }
    }, [maxHeight, surfaceOnly]);

    useEffect(() => {
        if (anchorMargin) {
            if (!surfaceOnly) {
                mdcMenu.current.menuSurface_.foundation.setAnchorMargin(
                    anchorMargin
                );
            } else {
                mdcMenu.current.foundation.setAnchorMargin(anchorMargin);
            }
        }
    }, [anchorMargin, surfaceOnly]);

    useEffect(() => {
        if (handleBodyClick) {
            mdcMenu.current.handleBodyClick = handleBodyClick;
        }
    }, [handleBodyClick]);

    useEffect(() => {
        if (
            (!fixed && absolutePositionX && absolutePositionX > -1) ||
            (!fixed && absolutePositionY && absolutePositionY > -1)
        ) {
            mdcMenu.current.setAbsolutePosition(
                absolutePositionX,
                absolutePositionY
            );
            if (
                mdcMenu.current.foundation.getAutoLayoutmeasurements()
                    .surfaceSize
            ) {
                mdcMenu.current.foundation.autoposition();
            }
        }
    }, [absolutePositionX, absolutePositionY, fixed]);

    useEffect(() => {
        mdcMenu.current.setFixedPosition(fixed);
    }, [fixed]);

    useEffect(() => {
        mdcMenu.current.wrapFocus = wrapFocus;
    }, [wrapFocus]);

    useEffect(() => {
        if (anchorCorner && !(absolutePositionX || absolutePositionY)) {
            mdcMenu.current.setAnchorCorner(Corner[anchorCorner.toUpperCase()]);
        }
    }, [mdcMenu, anchorCorner, absolutePositionX, absolutePositionY]);

    useEffect(() => {
        mdcMenu.current.setIsHoisted(hoistToBody);
        if (surfaceOnly) {
            mdcMenu.current.setMenuSurfaceAnchorElement(anchorRef.current);
        } else {
            mdcMenu.current.setAnchorElement(anchorRef.current);
        }
    }, [hoistToBody, surfaceOnly, trigger]);

    useEffect(() => {
        mdcMenu.current.quickOpen = quickOpen;
    }, [quickOpen]);

    useEffect(() => {
        // State is controlled both above and below this component
        // The only time we want to update MDC state is when `open` is true.
        // Updating when `open` is false fires too many `closed` events
        if (open) {
            if (!surfaceOnly && !mdcMenu.current.open) {
                mdcMenu.current.open = open;
            } else if (!mdcMenu.current.foundation.isSurfaceOpen) {
                mdcMenu.current.open();
            }
        } else if (!surfaceOnly && mdcMenu.current.open) {
            mdcMenu.current.open = false;
        } else if (mdcMenu.current.foundation.isSurfaceOpen) {
            mdcMenu.current.close();
        }
    }, [mdcMenu, open, surfaceOnly]);

    const value = useMemo(() => ({ inMenu: true }), []);

    const classes = useMemo(
        () =>
            clsx(
                'lmnt',
                'mdc-menu-surface',
                'lmnt-menu-container',
                ((surfaceOnly && mdcMenu.current?.foundation.isOpen()) ||
                    (!surfaceOnly &&
                        mdcMenu.current?.menuSurface_.foundation.isOpen())) &&
                    'mdc-menu-surface--open',
                openUp && 'lmnt-menu-open-up',
                !surfaceOnly && 'mdc-menu',
                !hoistToBody && fullWidth && 'mdc-menu-surface--fullwidth',
                hoistToBody && fullScreen && 'lmnt-menu-surface--fullScreen',
                `lmnt-theme-${themeColor}-bg`,
                className
            ),
        [
            className,
            fullScreen,
            fullWidth,
            hoistToBody,
            openUp,
            surfaceOnly,
            themeColor
        ]
    );

    const createTriggerWrapper = useMemo(
        () => triggerWrapper(anchorProps, trigger, anchorRef),
        [anchorProps, trigger]
    );

    const createPortalWrapper = useMemo(
        () => portalWrapper(hoistToBody),
        [hoistToBody]
    );

    const styles = useMemo(() => ({ ...style, direction }), [style, direction]);
    return (
        <ConditionalWrapper condition={trigger} wrapper={createTriggerWrapper}>
            <ConditionalWrapper
                condition={hoistToBody}
                wrapper={createPortalWrapper}
            >
                <div
                    ref={ref}
                    id={idActual}
                    style={styles}
                    className={classes}
                    tabIndex='-1'
                    {...customProps}
                >
                    <MenuContext.Provider value={value}>
                        {children}
                    </MenuContext.Provider>
                </div>
            </ConditionalWrapper>
        </ConditionalWrapper>
    );
});

Menu.displayName = 'Menu';
Menu.propTypes = MenuPropTypes;
Menu.defaultProps = MenuDefaultProps;

export default Menu;
