import React, { forwardRef } from 'react';
import clsx from 'clsx';
import {
    MenuAnchorPropTypes,
    MenuAnchorDefaultProps
} from './generated/propTypes';

const MenuAnchor = forwardRef((props, ref) => {
    const { children, className, ...customProps } = props;
    return (
        <div
            ref={ref}
            className={clsx('lmnt', 'mdc-menu-surface--anchor', className)}
            {...customProps}
        >
            {children}
        </div>
    );
});

MenuAnchor.displayName = 'MenuAnchor';
MenuAnchor.propTypes = MenuAnchorPropTypes;
MenuAnchor.defaultProps = MenuAnchorDefaultProps;

export default MenuAnchor;
